/* ----------- General Styles ----------- */
body {
  margin: 0; /* Removes default margin */
  background-color: #fdf4ef; /* Light beige background color */
  font-family: 'Quicksand', sans-serif; /* Font used across the site */
}

/* ----------- Main Container ----------- */
.gallery-wrapper {
  padding: 2rem; /* Adds space around the content */
  text-align: center; /* Centers text inside */
}

/* ----------- Titles ----------- */
.title {
  font-size: 2.5rem; /* Large font size for main titles */
  color: #2c2c2c; /* Dark grey text color */
  margin-bottom: 0.2rem; /* Adds space below the title */
  text-align: center; /* Centers the title */
}

.subtitle {
  color: #666; /* Medium grey color for subtitles */
  margin-bottom: 2rem; /* Adds space below the subtitle */
  text-align: center; /* Centers the subtitle */
}

/* ----------- Search Bar ----------- */
.search-bar {
  display: flex; /* Flexbox layout */
  justify-content: center; /* Centers the content horizontally */
  gap: 8px; /* Adds space between input and button */
  margin-bottom: 2rem; /* Adds space below the search bar */
}

.search-bar input {
  padding: 10px 15px; /* Adds padding inside the input */
  border-radius: 10px; /* Rounds the corners of the input */
  border: none; /* Removes border */
  width: 200px; /* Defines width of the input */
}

.search-bar button {
  padding: 10px 15px; /* Adds padding inside the button */
  background-color: #d88f6a; /* Background color for the button */
  border: none; /* Removes the border */
  color: white; /* White text color */
  border-radius: 10px; /* Rounds the corners of the button */
  cursor: pointer; /* Changes the cursor to a pointer on hover */
}

.searchinfo {
  text-align: center; /* Centers the text */
  color: #d88f6a; /* Button color for the info text */
  margin-top: -10px; /* Reduces space above the text */
  padding-bottom: 10px; /* Adds padding below the text */
  font-style: italic; /* Italicized font */
  font-size: 0.9rem; /* Slightly smaller font size */
}

/* ----------- Custom Masonry Grid ----------- */
.grid {
  display: grid; /* Use grid layout */
  grid-template-columns: repeat(3, 1fr); /* Three equal-width columns */
  column-gap: 5px; /* Adds space between columns */
  row-gap: 5px; /* Adds space between rows */
  max-width: 600px; /* Limits the width of the grid */
  margin: 0 auto; /* Centers the grid */
  cursor: pointer; /* Changes cursor on hover */
  transition: transform 0.3s ease; /* Smooth transition for transformation */
  padding: 0 15px; /* Ajout d'un padding sur les côtés */
}

.grid img {
  width: 100%; /* Makes image fill its container */
  object-fit: cover; /* Ensures image covers area without distortion */
  border-radius: 12px; /* Rounded corners for images */
  background-color: #f4e9e0; /* Light beige background for images */
  display: block; /* Ensures image is displayed as a block element */
  box-shadow: 0 6px 12px rgba(0, 0, 0, 0.3); /* Default shadow for images */
  transition: transform 0.3s ease, box-shadow 0.3s ease; /* Smooth transition for hover effects */
}

.grid img:hover {
  transform: scale(1.1); /* Scales the image by 10% on hover */
  box-shadow: 0 12px 24px rgba(0, 0, 0, 0.4); /* Stronger shadow on hover */
}

/* ----------- Image Heights by Column ----------- */
/* Column 1 */
.grid img:nth-child(3n + 1) {
  height: 220px; /* Fixed height for images in column 1 */
}

/* Column 2 — smaller images */
.grid img:nth-child(3n + 2) {
  height: 220px; /* Fixed height for images in column 2 */
}

/* Column 3 */
.grid img:nth-child(3n) {
  height: 220px; /* Fixed height for images in column 3 */
}

/* Force image 10 to be in the second column */
.grid img:nth-child(10) {
  grid-column: 2; /* Moves it to column 2 */
  height: 130px; /* Matches height of column 2 images */
}

/* ----------- Lightbox Styles ----------- */
.lightbox {
  position: fixed; /* Fixes the lightbox to the screen */
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.8); /* Dark transparent background */
  display: flex; /* Uses flexbox for centering content */
  justify-content: center; /* Centers horizontally */
  align-items: center; /* Centers vertically */
  z-index: 1000; /* Makes sure it is above other elements */
}

.lightbox-content {
  position: relative; /* For positioning the close button */
  display: flex; /* Centers image inside lightbox */
  justify-content: center;
  align-items: center;
  padding: 20px; /* Adds padding around the image */
  max-width: 50%; /* Limits max width */
  max-height: 50%; /* Limits max height */
}

.lightbox img {
  max-width: 80%; /* Limits image width */
  max-height: 80%; /* Limits image height */
  border-radius: 8px; /* Rounded corners for images */
  object-fit: contain; /* Ensures image scales correctly without distortion */
}

.close-button {
  position: absolute; /* Fixes position to the top-right */
  top: 10px;
  right: 10px;
  background-color: #d88f6a; /* Close button color */
  color: white; /* White text color */
  border: none; /* No border */
  padding: 10px; /* Adds padding */
  font-size: 18px; /* Button text size */
  cursor: pointer; /* Pointer cursor on hover */
}

.close-button:hover {
  background-color: brown; /* Darker button color on hover */
}

footer {
  text-align: center; /* Centers footer text */
}

/* ----------- Loading Text ----------- */
.loading {
  text-align: center; /* Centers loading text */
  font-size: 1.5rem; /* Large font size */
  color: #d88f6a; /* Matches the theme color */
  margin-top: 20px; /* Adds space above the text */
}

/* ----------- Light Mode and Dark Mode ----------- */
.gallery-container.light-mode {
  background-color: #fdf4ef; /* Light mode background */
  color: #222222; /* Dark text for readability */
}

.gallery-container.dark-mode {
  background-color: #121212; /* Dark mode background */
  color: #f0f0f0; /* Light text for dark mode */
}

/* ----------- Theme Toggle Button ----------- */
.theme-toggle button {
  padding: 10px 20px;
  margin-top: 20px;
  border: none;
  cursor: pointer;
  border-radius: 5px;
  background-color: #f0f0f0; /* Default light background for button */
  color: #222; /* Default dark text color */
  transition: background-color 0.3s, color 0.3s; /* Smooth transition for theme change */
}

.gallery-container.dark-mode .theme-toggle button {
  background-color: #333; /* Dark background for button in dark mode */
  color: #fff; /* Light text for dark mode */
}

/* ----------- Search Bar in Dark Mode ----------- */
.search-bar input {
  padding: 8px;
  border: 1px solid #ccc;
  border-radius: 5px;
}

.gallery-container.dark-mode .search-bar input {
  background-color: #2c2c2c; /* Dark background in dark mode */
  color: #fff; /* Light text in dark mode */
  border: 1px solid #555; /* Darker border in dark mode */
}

/* ----------- Responsive Design ----------- */

/* For small screens (mobile): Single column */
@media (max-width: 600px) {
  .grid {
    grid-template-columns: 1fr; /* Single column layout */
  }

  .product-image {
    height: 200px; /* Adjust image height */
  }
}

/* For medium screens: Two columns */
@media (max-width: 900px) {
  .grid {
    grid-template-columns: repeat(2, 1fr); /* Two columns layout */
  }

  .product-image {
    height: auto;
    min-height: 180px; /* Adjust image height */
  }
}

/* For large screens: Three columns */
@media (min-width: 901px) {
  .grid {
    grid-template-columns: repeat(3, 1fr); /* Three columns layout */
  }

  .product-image {
    height: auto;
    min-height: 220px; /* Adjust image height */
  }
}
